import React, { useState, useEffect } from 'react'

import axios from 'axios'
import LayoutP from './LayoutP'

const Perfil = props => {
  const [user, setUser] = useState({})
  useEffect(() => {
    axios
      .get(`https://diegoye.herokuapp.com/perfil/${props.match.params.id}`)
      .then(({ data }) => {
        setUser(prevState => {
          return {
            ...prevState,
            ...data.user
          }
        })
      })
      .catch(err => {
        console.log(err)
      })
  }, [props.match.params.id])
  const follow = user.follow

  if (!follow) return <p>Loading</p>
  return (
    <LayoutP>
      <div className="principal3">
        <h2>Bienvenido {user.name}</h2>
        <img src={user.imgProfile} alt={user.name} width="200" height="150" />
        <br />
        <br />
        <h2>Universidades a las que sigues</h2>

        {follow.map((follow, i) => (
          <ul key={i}>
            <li>{follow.name}</li>
          </ul>
        ))}
      </div>
    </LayoutP>
  )
}

export default Perfil
